export const getYearsDropDown = (): IDataItem[] => {
  const currentYear = new Date().getFullYear()
  const result: IDataItem[] = []
  for (let i = -20; i < 6; i++) {
    const year = currentYear + i
    result.push({ value: year, title: year })
  }

  return result
}

